            project_id: this.project.project_id,
            schedule_id: e
          };
          this.loading = true;
          this.$http.post(this.URL.setProjectSchedule, getData)
            .then(res => {
              let data = res.data.data;
              this.$tool.success("");
              this.loading = false;
              this.project.pro_schedule.schedule_id=data.schedule_id;
              this.project.pro_schedule.schedule_name=data.schedule_name;

            })
            .catch(err => {
              this.$tool.console(err, 2);
              this.loading = false;
              this.$tool.error("");
            })
        }
        this.takechange=true;
      },//hold
      filterChangeCurrent(page){
        this.loading=true;
        this.getConCon.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
        this.currentPage=page;
        this.getConCon.project_id=this.project.project_id;
        this.getConCon.page=page;
        this.getConCon.schedule_id='';
        this.$http.post(this.URL.getEnjoyedInvestors,this.getConCon)
          .then(res=>{
            if(res.data.status_code==2000000) {
              let data = res.data.data;
              this.enjoyInvestors=this.setEnjoyInvestor(data);
              this.totalData = res.data.count;
            }
            this.loading = false;
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.loading=false;
            this.$tool.error("");
          })
      },//
      handleClick2(tab, event) {

        if(tab.name=="1") this.tabs=true;
        else this.tabs=false
      },//

      /**/
      getEchartData(){
        var getEchartData = new Promise((resolve, reject)=>{
          //
          this.$http.post(this.URL.getEnjoyedInvestorsGroup,{user_id:localStorage.user_id,project_id:this.project.project_id})
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.chartData=data;
                this.eChart(data.going,data.hold,data.reject);
                resolve(4);
              }
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.$tool.error("");
            })

        });
        return getEchartData;

      },//()
      getEnjoyedInvestors(){
        var getEnjoyedInvestors = new Promise((resolve, reject)=>{
          //
          this.getConCon.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
          this.currentPage=1;
          this.getConCon.project_id=this.project.project_id;
          this.getConCon.page=1;
          this.getConCon.schedule_id='';
          this.$http.post(this.URL.getEnjoyedInvestors,this.getConCon)
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.enjoyInvestors=this.setEnjoyInvestor(data);
                this.totalData = res.data.count;
                if(this.enjoyInvestors.length==0) this.activeName='2';
                resolve(5);
              }
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.$tool.error("");
            })
        });
        return getEnjoyedInvestors;

      },//
      setEnjoyInvestor(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          let obj = new Object;
          obj.follow_id=x.follow_id;
          obj.user_id=x.card.user_id;
          obj.card_id=x.card.card_id;
          obj.user_real_name=x.card.user_real_name;
          obj.is_add=x.card.is_add;
          obj.is_bind=x.card.is_bind;
          obj.schedule_id=x.schedule.schedule_id;
          obj.user_invest_industry=x.card.user_invest_industry;
          obj.user_invest_stage=x.card.user_invest_stage;
          obj.type=x.type;
          obj.user_avatar_url=x.card.user_avatar_url;
          obj.user_avatar_txt=this.$tool.setUrlChange(x.card.user_avatar_url,x.card.user_real_name);
          obj.user_company_career=x.card.user_company_career;
          obj.user_company_name=x.card.user_company_name;
          obj.match=x.match;
          obj.user_group=this.$tool.setTagToString(x.card.user_group,'group_title');
          obj.width=this.selectChange(x.schedule.schedule_id);
          obj.source=x.source;
          newArr.push(obj);
        });
        return newArr;
      },//
      eChart(going,hold,reject){
        if(this.chartCheck) this.chart = this.$echart.init(document.getElementById('echart'));
        let option = {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            x: 'right',

            top:'30%',
            data:["","Hold","Rejcet"],
            textStyle:{
              fontSize:"16",
            }
          },
          series: [
            {
              name: '',
              type: 'pie',
              selectedMode: 'single',
              radius: ['70%','90%'],
              center: ['35%', '55%'],
              label: {
                normal: {
                  show: true,
                  position: 'center',
                  textStyle:{
                    fontSize:"16",
                  }
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '20',
                    fontWeight: 'bold'
                  }
                }
              },
              data: [
                {value: going, name: ''},
                {value: hold, name: 'Hold'},
                {value: reject, name: 'Rejcet'},
              ],
              itemStyle:{
                normal:{
                  label:{
                    show: true,
                    formatter: '{b} : {c}'
                  },
                  labelLine :{show:true}
                }
              }
            }
          ]
        };
        this.chart.setOption(option);
        this.chartCheck=false;
      },//
      getIndex(index){
        this.scheduleIndex=index;
      },//
      selectChange(e){
        let width = 0;
        switch (e){
          case 1:
            width=10;
            break;
          case 2:
            width=20;
            break;
          case 3:
            width=30;
            break;
          case 4:
            width=40;
            break;
          case 5:
            width=50;
            break;
          case 6:
            width=60;
            break;
          case 7:
            width=70;
            break;
          case 8:
            width=0;
            break;
          case 9:
            width=0;
            break;
          default:
            alert("")
            break;
        }
        let index = this.scheduleIndex;
        if(index!=-1) {
          this.enjoyInvestors[index].width = width;
          let follow_id = this.enjoyInvestors[index].follow_id;
          let schedule_id = this.enjoyInvestors[index].schedule_id;
          this.$http.post(this.URL.setEnjoyProjectSchedule, {
            user_id: localStorage.user_id,
            follow_id: follow_id,
            schedule_id: schedule_id
          })
            .then(res => {
              this.$tool.success("");
              this.scheduleIndex=-1;
              this.loading = false;
              this.getEchartData();
            })
            .catch(err => {
              this.$tool.console(err, 2);
              this.loading = false;
              this.$tool.error("");
            })
        }
        return width;
      },//
      selectSearch(e){
        this.loading=true;
        this.getConCon.schedule_id=e;
        this.getConCon.user_id=localStorage.user_id;
        this.currentPage=1;
        this.getConCon.project_id=this.project.project_id;
        this.getConCon.page=1;
        this.$http.post(this.URL.getEnjoyedInvestors,this.getConCon)
          .then(res=>{
            if(res.data.data.length!=0){
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.enjoyInvestors=this.setEnjoyInvestor(data);
                this.totalData = res.data.count;

              }
            }else{
              this.enjoyInvestors=[];
              this.totalData = 0;
            }

            this.loading = false;
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.loading=false;
            this.$tool.error("");
          })

      },//

      /**/
      setProjectMatchInvestors(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          let obj = new Object;
          obj.match=x.match;
          obj.is_follow=x.is_follow;
          obj.type=x.type;
          obj.push_statues=x.push_statues;
          obj.user_avatar_url=x.card.user_avatar_url;
          obj.user_avatar_txt=this.$tool.setUrlChange(x.card.user_avatar_url,x.card.user_real_name);
          obj.user_real_name=x.card.user_real_name;
          obj.user_company_career=x.card.user_company_career;
          obj.user_company_name=x.card.user_company_name;
          obj.user_invest_industry=x.card.user_invest_industry;
          obj.user_invest_stage=x.card.user_invest_stage;
          obj.user_id=x.card.user_id;
          obj.card_id=x.card.card_id;
          obj.user_eamil=x.card.user_email;
          obj.investor_id=x.card.investor_id;

          obj.user_group=this.$tool.setTagToString(x.card.user_group,'group_title');
          newArr.push(obj);
        });
        return newArr;
      },//
      getProjectMatchInvestors(){
        var getProjectMatchInvestors = new Promise((resolve, reject)=>{
          //
          this.getInvestors.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
          this.currentPageInvestors=1;
          this.getInvestors.project_id=this.project.project_id;
          this.getInvestors.page=1;
          this.$http.post(this.URL.getProjectMatchInvestors,this.getInvestors)
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.ProjectMatchInvestors=this.setProjectMatchInvestors(data);
                this.totalInvestors = res.data.count;
                resolve(6);
              }
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.$tool.error("");
            })
        });
        return getProjectMatchInvestors;
      },//
      filterChangeInvestors(page){
        this.loading=true;
        this.getInvestors.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
        this.currentPageInvestors=page;
        this.getInvestors.project_id=this.project.project_id;
        this.getInvestors.page=page;
        this.$http.post(this.URL.getProjectMatchInvestors,this.getInvestors)
          .then(res=>{
            if(res.data.status_code===2000000) {
              let data = res.data.data;
              this.ProjectMatchInvestors=this.setProjectMatchInvestors(data);
              this.totalInvestors = res.data.count;
            }
            this.loading = false;
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.loading=false;
            this.$tool.error("");
          })
      },//
      industryPush(data){
        if(data==0){
          this.$tool.warning("")
        }else{
          console.log(data)
          this.littlePushShow=true;
          this.littlePush.email=data.user_eamil;
          if(data.type==='user'){
            this.pushData=[data.user_id,data.type]
          }else{
            this.pushData=[data.card_id,data.type]
          }
        }
      },//
      industryDelete(data){
        let delData = new Object;
        delData.user_id = localStorage.user_id;
        delData.investor_id = data.investor_id;
        delData.project_id = this.project.project_id;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.loading = true;
          this.$http.post(this.URL.exceptMatchAction, delData)
            .then(res => {
              if (res.data.status_code == 2000000) {
                this.$tool.success("");
                this.getProjectMatchInvestors();
              }
              this.loading = false;
            })
            .catch(err => {
              this.$tool.console(err, 2);
              this.loading = false;
              this.$tool.error("");
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },//
      selectFollow(e){
        this.getInvestors.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
        this.currentPageInvestors=1;
        this.getInvestors.project_id=this.project.project_id;
        this.getInvestors.page=1;
        this.getInvestors.is_follow=e;
        this.$http.post(this.URL.getProjectMatchInvestors,this.getInvestors)
          .then(res=>{
            if(res.data.status_code==2000000) {
              let data = res.data.data;
              this.ProjectMatchInvestors=this.setProjectMatchInvestors(data);
              this.totalInvestors = res.data.count;
            }
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.$tool.error("");
          })
      },//
      /**/
      getFollowId(id){
        this.dialogFollow=true;
        this.followid=id;
        this.getFollowData=false;
      },//id
      littlePushCertain(){
        if(!this.littlePush.email){
          this.$tool.error('')
        }else if(!this.$tool.checkEmail(this.littlePush.email)){
          this.$tool.error('')
        }else{
          this.pushData.push(this.littlePush.email)
          //
          let newPushData=[];
          newPushData.push(this.pushData)
          this.$http.post(this.URL.pushProject,{
            user_id:localStorage.user_id,
            project_id:this.project.project_id,
            title:this.littlePush.content,
            body:'',
            receives:newPushData
          }).then(res=>{
            if(res.data.status_code===2000000){
              this.$tool.success('');
              this.$refs['littlePush'].resetFields();
              this.littlePushShow=false;
              this.pushData.pop()
            }else{
              this.$tool.error(res.data.error_msg)
              this.pushData.pop();
            }
          }).catch(err=>{
            this.pushData.pop();
          })
        }
      },//
      littlePushCancel(){
        this.$refs['littlePush'].resetFields();
        this.littlePushShow=false;
      },//

      /**/
      dialogPrechange(msg){
        this.dialogPreviewVisible=msg;
      },//
      previewPush(x){
        this.emitPush=x;
      },//
      getNewPro(){
        var getNewPro = new Promise((resolve, reject)=>{
          //
          this.$global.func.getWxProjectCategory();
          resolve(1);
        });
        return getNewPro;
      },
      closePreview(msg){
        this.dialogPreviewVisible=msg;
      },//
    },
    created () {
      this.loading=true;
      this.getprojectId();

      this.getNewPro()
      .then((data)=>{
        return this.getWxProjectCategory();
      })
      .then((data)=>{
        return this.getProjectDetail();
      })
      .then((data)=>{
        return this.getEchartData();
      })
      .then((data)=>{
        return this.getEnjoyedInvestors();
      })
      .then((data)=>{
        this.loading=false;
        return this.getProjectMatchInvestors();
      });

    },
    watch: {
      '$route' (to, from) {

      }
    },
  }
</script>

<style lang="less">
  @import '../../../assets/css/projectDetail.less';
.scheduleColor{
  color:#20a0ff!important;
}
  #projectDetails{

    .btn1{
      background:#40587a;
      border-radius:2px;
      width:88px;
      height:36px;color:#ffffff;cursor: pointer;margin-left: 16px;
    }
    .el-dialog__header{
      padding-left:21px;
    }
    .onsearch{
      width: 100%;
      max-height: 320px;
      overflow-y: auto;
      li{
      width: 100%;
        height:40px;
        line-height: 40px;
        font-size:13px;
        color:#475669;
        text-align:left;
        cursor: pointer;
        padding-left: 20px;
      }
      li:hover{
        background:#009eff;
        color:#ffffff;
      }
    }
    .el-tabs__content{
     position: static;
    }

/*    .radio_line{
      width: 13px;
      //height: 49px;
    }
    .radio{
      border:1px solid #e0e6ed;
      border-radius:20px;
      width:12px;
      background: #f9fafc;
      height:12px;
      position: relative;
    }*/
    .littlePush{
      .el-dialog--small{
        width:430px;
      }
      .el-form-item__label{
        text-align: left;
      }
      .pushCount{
        position: absolute;
        left: 80px;
        top: 24px;
        font-size: 12px;
        span{
          color: red;
        }
      }
    }
  }
</style>



