<template>
  <!---->
  <div id="projectPush" v-loading.fullscreen.lock="loading" element-loading-text="">
    <el-dialog :visible="dialogPush" :before-close="handleClose">
      <!---->
      <span slot="title" class="dialog-title clearfix">
        <div class="lines fl"></div>
        <div class="title fl"></div>
        <div class="lost fl"><i>{{pushCount}}</i></div>
        <div class="img fl"><img src="../../../assets/images/why.png"></div>
      </span>

      <!---->
      <div>
        <div class="text1"></div>
        <div class="text2">{{project_name}}</div>
      </div>

      <!---->
      <el-form :inline="true" :model="investor" class="demo-form-inline pushInvestor" label-position="top">
        <el-form-item label="">
          <el-select v-model="allShow" filterable
                     style="width: 586px;"
                     remote placeholder=""
                     multiple @remove-tag="removeTag"
                     :remote-method="remoteMethod"
                     popper-class="popper">
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button style="margin-top: 25px;" type="primary" @click="customerAdd"></el-button>
        </el-form-item>
      </el-form>

      <!--tab-->
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <!---->
          <el-tab-pane label="" name="myContacts">
            <el-table
              v-loading="loading"
              element-loading-text=""
              ref="myContacts"
              :data="myContacts"
              tooltip-effect="dark"
              style="width: 100%;font-size: 12px!important;"
              max-height="430"
              :row-class-name="tableRowClassName">
              <!---->
              <el-table-column width="64" v-if="reBorn">
                <template scope="scope">
                    <el-checkbox :checked="myCheckList[scope.row.card.card_id]" :label="scope.row.card.card_id" @change="myCheck" :name="scope.row.card.user_real_name+'( '+scope.row.card.user_email+' )'"></el-checkbox>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                prop="name"
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_real_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_real_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_career}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_career}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_email}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_email}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt" >{{scope.row.card.user_invest_industry_str}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_invest_industry_str}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.match}}</div>
                    </div>
                    <div>
                      {{scope.row.match}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>

          <!---->
          <el-tab-pane label="" name="netContacts">
            <el-table
              v-loading="loading"
              element-loading-text=""
              ref="netContacts"
              :data="netContacts"
              tooltip-effect="dark"
              style="width: 100%;font-size: 12px;"
              max-height="430"
              :row-class-name="tableRowClassName">
              <el-table-column width="64"  v-if="reBorn">
                <template scope="scope">
                  <el-checkbox :checked="netCheckList[scope.row.card.user_id]"  :label="scope.row.card.user_id"   @change="netCheck" :name="scope.row.card.user_real_name+'( '+scope.row.card.user_email+' )'"></el-checkbox>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_real_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_real_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_career}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_career}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_email}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_email}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_invest_industry_str}}</div>
                    </div>
                    <div>{{scope.row.card.user_invest_industry_str}}</div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.match}}</div>
                    </div>
                    <div>
                      {{scope.row.match}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>

      <!---->
      <el-form label-position="top" label-width="80px" ref="email" :model="email">
        <el-form-item label="" prop="title" style="margin-top: 30px">
          <el-input v-model="pushTitle" placeholder="-"></el-input>
        </el-form-item>
        <el-form-item label="" prop="main">
          <el-input type="textarea"
                    v-model="pushBody"
                    placeholder=""
                    :autosize="{ minRows: 4, maxRows: 7}"></el-input>
        </el-form-item>
      </el-form>

      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="preview"></el-button>
        <el-button type="primary" @click="push"></el-button>
      </span>

      <!---->
      <!--<customeraddcontacts :dialog-form-visible="dialogFormVisible"></customeraddcontacts>-->

      <!--2-->
      <el-dialog class="customerAddForm"  title="" :visible.sync="dialogFormVisible" :modal='false' size="full" :close-on-click-modal="false">
        <el-form :model="customerAddForm" ref="customerAddForm">
          <el-form-item label="" :label-width="formLabelWidth" prop="email" :rules="[{ required: true, message: ''}]">
            <el-input v-model="customerAddForm.email" auto-complete="off"  placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="name">
            <el-input v-model="customerAddForm.name" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
            <el-input v-model="customerAddForm.mobile" auto-complete="off" placeholder="" :rules="[{ required: false}]" type="number"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="company">
            <el-input v-model="customerAddForm.company" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="brand">
            <el-input v-model="customerAddForm.brand" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="career">
            <el-input v-model="customerAddForm.career" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelAdd"> </el-button>
          <el-button type="primary" @click="certainAdd"> </el-button>
        </div>
      </el-dialog>
    </el-dialog>
<!---->
  </div>
</template>

<script type="text/ecmascript-6">
  import customerAddContacts from '../../../components/customerAddContacts.vue'
  import { mapState } from 'vuex'
  export default {
  props: ["dialogPush",'proid','proName','emitPush'],
  data () {
    return {
      project_name:this.proName,
      project_id:this.proid,
      close:false,//
      loading: false,
      //tab
      activeName: 'myContacts',
      //
      myContacts:[],
      //
      netContacts:[],
      //(show:name;idid.computed)
      myContactsShow:[],
      netContactsShow:[],
      //
      pushData:[],
      //
      myCheckList:{},
      netCheckList:{},
      //
      dialogFormVisible:false,
      //
      customerAddForm: {
        email:'',
        name: '',
        mobile:'',
        company:'',
        brand:'',
        career:'',
      },
      formLabelWidth: '74px',
      //
      pushCount:5,
      //
      pushTitle:'',
      pushBody:'',
      //tab
      activeTab:'myContacts',
      //input
      filterString:'',
      investor:{
        name:'',
      },
      //
      reBorn:false,

      email:{
        title:',',//
        main:'',//
      },
      email2:{
        nameEmail:'',//()
      },
      user:{
        user_real_name:'',
        user_company_career:'',
        user_company_name:'',
      },
    }
  },
  components:{customerAddContacts},
  methods: {
    //
    getFirstUser(user){
      this.$http.post(this.URL.getOneUserInfo,{user_id: localStorage.user_id})
      .then(res=>{
        if(res.data.status_code==2000000) {
          let data = res.data.data;
          user.firse_user_real_name=data.user_real_name;
          user.firse_user_company_career=data.user_company_career;
          user.firse_user_company_name=data.user_company_name;
        }
      })
      .catch(err=>{
        this.$tool.console(err,2);
        this.$tool.error("");
      })
    },
    //
    getMyContacts(remote){
      this.$http.post(this.URL.getConnectUserSortByMatch, {
        user_id: localStorage.user_id,
        project_id: this.project_id,
        search:this.filterString,
      }).then(res => {
        if(res.data.status_code===2000000){
//          console.log('', res.data.data)
          this.myContacts=res.data.data;
          if(remote){
            //checkBox
            this.reBorn=false;
            setTimeout(()=>{
              this.reBorn=true;
            },0)
          }
        }else{
//          console.log(res.data.error_msg)
        }
      })
    },
    //
    getNetContacts(remote){
      this.$http.post(this.URL.getAllConnectUserSortByMatch, {
        user_id: localStorage.user_id,
        project_id: this.project_id,
        search: this.filterString,
      }).then(res => {
        if(res.data.status_code===2000000){
//          console.log('',res.data.data)
          this.netContacts=res.data.data;
          if(remote){
            //checkBox
            this.reBorn=false;
            setTimeout(()=>{
              this.reBorn=true;
            },0)
          }
        }else{
//          console.log(res.data.error_msg)
        }
      })
    },
    //
    getPushCount(){
      this.$http.post(this.URL.pushCount,{
        user_id:localStorage.user_id
      }).then(res=>{
        if(res.data.status_code===2000000){
          this.pushCount=res.data.data.push_count.remain_times;
        }
      })
    },
    //,,,,
    initData(){
      this.pushData=[];
      this.myContactsShow=[];
      this.netContactsShow=[];
      this.pushTitle='';
      this.pushBody='';
      this.pushData=[];
      this.myCheckList={};
      this.netCheckList={};
      this.getMyContacts();
      this.getNetContacts();
      this.getPushCount();
    },
    //
    handleClick(tab, event) {
//      console.log(tab.name)
      this.activeTab=tab.name
    },
    //
    tableRowClassName(row, index) {
      if (index%2 === 1) {
        return 'info-row';
      }
      return '';
    },
    //()
    customerAdd(){
     this.dialogFormVisible=true;
    },
    //
    cancelAdd(){
      this.$refs['customerAddForm'].resetFields();
      this.dialogFormVisible=false;
    },
    //
    certainAdd(){
      var form=this.customerAddForm;
      if(!form.email){
        this.$tool.error('')
      }else if(!this.$tool.checkEmail(form.email)){
        this.$tool.error('')
      }else{
        this.$http.post(this.URL.createUserCard,{
          user_id:localStorage.user_id,
          user_email:form.email,
          user_real_name:form.name,
          user_mobile:form.mobile,
          user_company_name:form.company,
          user_company_career:form.career,
          user_brand:form.brand
        }).then(res=>{
          console.log(res)
          if(res.data.status_code===2000000){
            this.$refs['customerAddForm'].resetFields();
            this.dialogFormVisible=false;
            this.getMyContacts();
            this.getNetContacts();
          }
        })
      }
    },
    //
    remoteMethod(query) {
      this.filterString=query;
      console.log(this.myCheckList)
      if(this.activeTab==="myContacts"){
        this.getMyContacts('remote')
      }else{
        this.getNetContacts('remote')
      }
    },
    //
    removeTag(e){
      let checktag=e.value
      let pattern =new RegExp("\\((.| )+?\\)","igm")
      let name=checktag.substring(0,checktag.indexOf('('));
      let email=checktag.match(pattern).toString();
      email=email.substring(2,email.length-2)

      //Input
      this.myContactsShow.forEach((x,index)=>{
        if(x===e.value){
          this.myContactsShow.splice(index,1)
        }
      })
      this.netContactsShow.forEach((x,index)=>{
        if(x===e.value){
          this.netContactsShow.splice(index,1)
        }
      })

      //checkboxmyCheck
      this.myContacts.forEach((x,index)=>{
        if(x.card.user_email===email){
          if(x.card.user_real_name===name){
            let thisId=x.card.card_id
            this.myCheckList[thisId]=!this.myCheckList[thisId]
            return
          }
        }
      })
      this.netContacts.forEach((x,index)=>{
        if(x.card.user_email===email){
          if(x.card.user_real_name===name){
            let thisId=x.card.user_id;
            this.netCheckList[thisId]=!this.netCheckList[thisId];
            return
          }
        }
      })


      /*//checkbox
      if(this.myCheckList[e.value]){
        this.myCheckList[e.value]=!this.myCheckList[e.value];
      }
      if(this.netCheckList[e.value]){
        this.netCheckList[e.value]=!this.netCheckList[e.value];
      }*/

      //pushData
      this.pushData.forEach((x,index)=>{
          if(x.card.user_email===email){
            if(x.card.user_real_name===name){
              this.pushData.splice(index,1)
            }
          }
      })
      console.log(this.pushData)
      //checkBox
      this.reBorn=false;
      setTimeout(()=>{
        this.reBorn=true;
      },0)
    },
    //
    nameFilter(x){
      if(x.card.user_real_name.indexOf(this.filterString)!=-1){
        return x
      }
    },
    //
    myCheck(e){
      let thisId=e.target.value;
      let thisName=e.currentTarget.name;
      if(this.myCheckList[thisId]===false){
        this.myContactsShow.push(thisName);
        this.myCheckList[thisId]=true;
        //
        this.myContacts.forEach(x=>{
            if(x.card.card_id===thisId){
              this.pushData.push(x)
              return
            }
        })
        //console.log(1,this.pushData)
      }else{
        let thisName=e.currentTarget.name;
        let thisId=e.target.value;
        this.myCheckList[thisId]=false;
        this.myContactsShow.splice(this.myContactsShow.indexOf(thisName),1)
        this.pushData.splice(this.pushData.indexOf(thisId),1)
      }
    },
    netCheck(e){
      let thisId=e.target.value;
      let thisName=e.currentTarget.name;
      if(this.netCheckList[thisId]===false){
        this.netContactsShow.push(thisName);
        this.netCheckList[thisId]=true;
        //
        this.netContacts.forEach(x=>{
          if(x.card.user_id===thisId){
            this.pushData.push(x)
            return
          }
        })
//        console.log(this.pushData)
      }else{
        let thisName=e.currentTarget.name;
        let thisId=e.target.value;
        this.netCheckList[thisId]=false;
        this.netContactsShow.splice(this.netContactsShow.indexOf(thisName),1)
        this.pushData.splice(this.pushData.indexOf(thisId),1)
      }
    },
    //
    preview(){
      if(this.pushData.length>0){
        let targetUser=this.pushData[0].card
        let user={
          user_real_name:targetUser.user_real_name,
          user_company_career:targetUser.user_company_career,
          user_company_name:targetUser.user_company_name,
          firse_user_real_name:'',//
          firse_user_company_career:'',
          firse_user_company_name:'',
        };
        this.getFirstUser(user);
        if(this.pushCount!=0) {
          this.$store.state.pushProject.project_id = this.project_id;
          this.$store.state.pushProject.user = user;
          this.$store.state.pushProject.pushMessage.user_id = localStorage.user_id;
          this.$store.state.pushProject.pushMessage.card_id = user.card_id;
          this.$store.state.pushProject.pushMessage.investor_id=user.investor_id;
          this.$store.state.pushProject.pushMessage.email = this.email2.nameEmail;
          this.$store.state.pushProject.pushMessage.title = this.pushTitle;
          this.$store.state.pushProject.pushMessage.body = this.pushBody;
          this.$store.state.pushProject.pushMessage.project_ids = new Array;
          this.$store.state.pushProject.pushMessage.project_ids.push(this.projectRadio);
          this.$store.state.pushProject.email.title = this.pushTitle;
          this.$store.state.pushProject.email.body = this.pushBody;
          this.$emit('preview', true);
        }else{
          this.$tool.warning("")
        }
      }else{
        this.$tool.error(' ')
      }
    },
    //
    push(){
      let dealData=[]
      this.pushData.forEach(x=>{
        if(x.type==='card'){
          dealData.push([x.card.card_id,x.type,x.card.user_email])
        }else{
          dealData.push([x.card.user_id,x.type,x.card.user_email])
        }
      })
      if(dealData.length===0){
        this.$tool.error('')
      }else if(dealData.length>this.pushCount){
        console.log(dealData.length,this.pushCount)
        this.$tool.error('')
      }else{
        this.$http.post(this.URL.pushProject,{
          user_id:localStorage.user_id,
          project_id:this.project_id,
          title:this.pushTitle,
          body:this.pushBody,
          receives:dealData
        }).then(res=>{
          if(res.data.status_code===2000000){
            this.$tool.success('');
            this.initData();
            this.$emit('changeClose',false);
          }
        })
      }
    },
    //
    handleClose(){
      this.initData();
      this.myContactsShow=[];
      this.netContactsShow=[];
      for(let x in this.myCheckList){
          this.myCheckList[x]=false;
      }
      for(let x in this.netCheckList){
        x=false;
      }
      this.$emit('changeClose',false);
    },
  },
  computed: mapState({
    allShow(){
      var allShow=[];
      return allShow.concat(this.myContactsShow,this.netContactsShow)
    },
  }),
  mounted(){},
  created(){

  },
  watch:{
    //
    dialogPush:function(e){
      if(e===true){
        this.project_name=this.proName;
        this.project_id=this.proid;
        this.initData();
        //checkBox
        if(e===true){
          this.reBorn=true
        }else{
          this.reBorn=false
        }
        //
        this.pushData=[];
        //
        if(this.project_id){
          this.$http.post(this.URL.getConnectUserSortByMatch, {
            user_id: localStorage.user_id,
            project_id: this.project_id,
            search:this.filterString,
          }).then(res => {
            if(res.data.status_code===2000000){
              res.data.data.forEach(x=>{
                this.myCheckList[x.card.card_id]=false;
              })
              this.myContacts=res.data.data;
              //,
              if(res.data.data.length===0){
                this.activeTab='netContacts'
              }
            }else{
//          console.log(res.data.error_msg)
            }
          })
          this.$http.post(this.URL.getAllConnectUserSortByMatch, {
            user_id: localStorage.user_id,
            project_id: this.project_id,
            search: this.filterString,
          }).then(res => {
            if(res.data.status_code===2000000){
              res.data.data.forEach(x=>{
                this.netCheckList[x.card.user_id]=false;
              })
              this.netContacts=res.data.data;
            }else{
//          console.log(res.data.error_msg)
            }
          })
        }
      }
    },
    //
    emitPush:function(e){
      this.push();
      this.$emit('changeClose',false)
    }
  }
}
</script>

<style lang="less">
  @import '../../../assets/css/mycontacts';
  .el-table .cell .el-tooltip{
    max-width: 105px!important;
    overflow: hidden!important;
    white-space: nowrap!important;
    text-overflow: ellipsis!important;
    word-break: break-all!important;
    line-height: 24px!important;
  }
  .el-table td, .el-table th{
    height:60px!important;
  }
  .popper{
    display: none;
  }
</style>



