<script type="text/ecmascript-6">
  import companyMessage from './onkeyresearch/companyMessage.vue'
  import business from './onkeyresearch/business.vue'
  import downloadechart from './onkeyresearch/downloadEchart.vue'
  export default {
    props: ["dialogVisible","companyId","compName"],
    data () {
      return {
        compname: "",//
        com_id: 0,//Id
        conmanyName: '3',
        productMessage: '',
        recruitMessage: '',
        recruitData: [{
          position: 'JAVA',
          money: '20-30K',
          experience: '1-2',
          address: "",
          date: '2016-05-04'
        }, {
          position: 'IOS',
          money: '1-2K',
          experience: '1',
          address: "",
          date: '2016-05-04'
        }],

        /**/
        project: [
          {
            project_id: 1,
            com_id: 4,
            company_name: "",
            project_name: "",
            project_industry: "",
            project_introduce: "",
            project_label: [],
            project_website: "",
            project_logo: "",
            project_score: ""
          }
        ],
        /**/
        news: [{
          project_id: "",//ID
          project_news_time: "",//
          project_news_label: "",//
          project_news_title: "",//
          source: ""//
        }],
        /**/
        competing: [
          {
            com_id: "",//
            project_id: "",//ID
            competing_goods_name: "",//
            competing_goods_logo: "",//LOGO
            competing_goods_industry: ",",//
            competing_goods_Set_up: "2014-07",//
            competing_goods_Financing_rounds: "",//
            competing_goods_region: "",//
            competing_goods_Financing_amount: "",//
            competing_goods_Financing_time: "2017-07",//
            competing_goods_label: "",//
            ranking_day: "",//
            competing_founder: "",//
          }
        ],
        /**/
        history_finance: [
          {
            com_id: "",//id
            History_financing_time: "2017.05.04",//
            History_financing_rounds: "A",//
            History_financing_money: "100-500",//
            History_financing_who: "",//
          }
        ],
        /**/
        milestone_list: [
          {
            project_id: "",//ID
            milestone_event: " P2P1.5B1.5B",//
            milestone_time: "2017.05.04",//
          }
        ],
        /**/
        team: [
          {
            project_id: "",//ID
            team_member_introduce: "",//
            team_member_photo: "",//
            team_member_name: "",//
            team_member_position: "",//
          }
        ],
        loading:false

      }
    },
    methods: {
      dialogVisibleTo() {
        this.$emit('changeall', false)
      },
      handleClose(done) {
        done();
        this.dialogVisibleTo()
      },
      goToEdit(){
//      this.$router.push({name: 'editproject', query: {}})
        this.$emit('changeall', false)
        this.$emit('changeallin', true)
      },
      loadMore(){
        this.recruitData.push({
          position: 'IOS',
          money: '1-2K',
          experience: '1',
          address: "",
          date: '2016-05-04'
        }, {
          position: 'IOS',
          money: '1-2K',
          experience: '1',
          address: "",
          date: '2016-05-04'
        })
      },
      getCrawlerTeam(){
          this.loading=true;
        this.$http.post(this.URL.getCrawlerTeam, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
          this.team = res.data.data;
          this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getCrawlerHistoryFinance(){
        this.loading=true;
        this.$http.post(this.URL.getCrawlerHistoryFinance, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
          let data=res.data.data;
          this.$tool.setTime(data,'history_financing_time');
          this.history_finance = data;
          this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getCrawlerMilestone(){
        this.loading=true;
        this.$http.post(this.URL.getCrawlerMilestone, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
          let data=res.data.data;
          this.$tool.setTime(data,'milestone_time');
          this.milestone_list = data;
          this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getCrawlerNews(){
        this.loading=true;
        this.$http.post(this.URL.getCrawlerNews, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
            let data=res.data.data;
            this.$tool.setTime(data,'project_news_time');
            this.news =data;
            this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getCrawlerCompeting(){
        this.loading=true;
        this.$http.post(this.URL.getCrawlerCompeting, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
          let data=res.data.data;
          this.$tool.setTime(data,'competing_goods_Financing_time');
          this.competing =data;
          this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getCrawlerProject(){
        this.loading=true;
        this.$http.post(this.URL.getCrawlerProject, {
          user_id: localStorage.user_id,
          com_id: this.com_id
        })
        .then(res => {
          this.getProjectIndustry(res.data.data);
          this.project = res.data.data;
          this.loading=false;
        })
        .catch(err => {
          this.$tool.console(err);
          this.loading=false;
        })
      },//
      getProjectIndustry(data){
        for(let i=0; i<data.length; i++){
          data[i].project_industry=data[i].project_industry.split(",");
        }
      },//
    },
    computed: {},
    components: {
      companyMessage,
      business,
      downloadechart
    },
    created(){

    },

    watch : {
      companyId : function(e){
        this.loading=true;
        this.com_id=e;
        this.compname=this.compName;
        this.getCrawlerTeam();
        this.getCrawlerHistoryFinance();
        this.getCrawlerMilestone();
        this.getCrawlerNews();
        this.getCrawlerCompeting();
        this.getCrawlerProject();

      },//id
      dialogVisible:function(e){

      }

    }
  }
</script>

<style lang="less">
  @import '../../../assets/css/onekeyresearch';
  .el-carousel__item img {
    width: 100%;
  }
  #research{


    .el-tabs__item{
      font-size:18px;
      color:#8492a6;
      line-height:20px;
    }
    .el-tabs__item.is-active{
      color:#1f2d3d;
    }
    .el-tabs__header{
      border:none;
    }
    .el-tabs__active-bar{
      display: none;
    }
  }
</style>



