<template>
  <div id="fileManagement" v-loading.fullscreen.lock="loading" element-loading-text="">
    <!---->
    <el-button class="createNewGroup" @click.prevent="toGroup"></el-button>
    <!---->
    <el-collapse v-model="activeNames">
      <el-collapse-item :name="index" v-for="(item,index) in groupList" :key="item.type_id">
        <!---->
        <template slot="title">
          <span class="clearfix collapseHead groupName">
            {{item.type_name}}  (<span>{{item.fileNum}}</span>)
             <el-button v-if="item.type_id>4" class="upload delete fr" type="text" @click.stop="getTypeId(item.type_id,3)"><img src="/static/images/filedelete.png"></el-button>
            <!--bp-->
             <div class="fr" v-if="item.type_id===1 && parseInt(item.fileNum)===0 && item.newFile.length===0">
               <el-upload
                 class="upload"
                 ref="upload"
                 :action="uploadAddress"
                 :on-change="handleChange"
                 :on-progress="uploadProgress"
                 :on-success="uploadsuccess"
                 :on-error="uploaderror"
                 :before-upload="beforeUpload"
                 :file-list="fileList"
                 :data="{user_id:this.localStorage.user_id,project_id:project_id}"
                 :show-file-list="false"
                 accept=".pdf, .ppt, .pptx, .doc, .docx, .rar, .zip">
                    <el-button class="upload" type="text" @click="getTypeId(item.type_id,2)"><img src="/static/images/shangchuan.png"></el-button>
               </el-upload>
            </div>
            <!--bp-->
            <div class="fr" v-if="item.type_id!=1">
               <el-upload
                 class="upload"
                 ref="upload"
                 :action="uploadAddressFile"
                 :on-change="handleChange"
                 :on-progress="uploadProgress"
                 :on-success="uploadsuccess"
                 :on-error="uploaderror"
                 :before-upload="beforeUpload2"
                 :file-list="fileList"
                 :data="{user_id:this.localStorage.user_id,project_id:project_id,type:item.type_id}"
                 :show-file-list="false"
                 accept=".pdf, .ppt, .pptx, .doc, .docx, .rar, .zip, .png, .jpg, .jpeg"
                 multiple>
                    <el-button class="upload" type="text" @click="getTypeId(item.type_id,2)"><img src="/static/images/shangchuan.png"></el-button>
               </el-upload>
            </div>
            <el-button v-if="item.type_id>4" class="upload rename fr" type="text" @click.stop="getTypeId(item.type_id,1,item.type_name)"><img src="/static/images/filerename.png"></el-button>
          </span>
        </template>
        <!---->
        <div class="fileList" style="padding-left: 37px">
          <div class="block-info block-cc-other" style="margin-bottom: 15px;"
               v-for="(file, index) in item.file"
               :key="file.file_id">
            <span class="f-name" style="cursor: pointer;font-size: 10px" @click="download(file.file_id)">{{file.file_title}}.{{file.file_ext}}</span>
            <div class="fr">
              <!--bp-->
              <el-button v-if="item.type_id===1" type="text"  @click="getFileId(file.file_id,item.type_id,'bp')"></el-button>
              <!--bp-->
              <el-dropdown v-if="item.type_id!=1"  @command="fileDeal" trigger="click">
                <span class="el-dropdown-link" @click="getFileId(file.file_id,item.type_id)">
                  <img :src="threePoint" class="threePoint">
                </span>
                <el-dropdown-menu slot="dropdown" class="curor">
                  <!--<el-dropdown-item command="1"></el-dropdown-item>-->
                  <el-dropdown-item command="2"></el-dropdown-item>
                  <el-dropdown-item command="3"></el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
        </div>
        <!---->
        <div class="fileList" style="padding-left: 37px">
          <div class="block-info block-cc-other" style="margin-bottom: 15px; position:relative;"
               v-for="(newF, index) in item.newFile"
               :key="newF.file_id">
            <span class="f-name" style="cursor: pointer">{{newF.name}}</span>
            <img src="../../../assets/images/loading.gif" style="width:16px;height: 16px;margin-left: 10px;">
            <span class="upLoading" v-loading.body="true"></span>
          </div>
        </div>
      </el-collapse-item>
    </el-collapse>
    <!---->
    <el-dialog title="" :visible.sync="dialogFileVisible" :show-close="showList">
      <el-form :model="newGroupName"  ref="newGroupName">
        <el-form-item label="" label-width="20px" prop="name"
                      :rules="[{max:20, message: '20',required: true, trigger: 'blur'}]">
          <el-row :span="24" :gutter="32">
            <el-col :span="23" style="padding-left: 0">
              <el-input v-model="newGroupName.name" auto-complete="off" placeholder="" style="border-radius: 2px!important"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="addGroup" style="background: #40587a;border-color: #40587a;margin-right:13px"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog class="moveFileFrame" title="" :visible.sync="fileMoveFrame">
        <el-radio-group v-model="radio">
          <el-radio v-if="group.type_id!=1" class="groupRadio" v-for="group in groupList" :key="group.type_id" :label="group.type_id">{{group.type_name}}</el-radio>
        </el-radio-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fileMoveFrame = false"> </el-button>
        <el-button type="primary" @click="fileMove"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="renameFrame" :show-close="showList">
      <el-form ref="renameFrame">
        <el-form-item label="" label-width="80px" prop="name">
          <el-row :span="24" :gutter="32">
            <el-col :span="18">
              <el-input v-model="exGroupName" auto-complete="off"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="renameCancel"> </el-button>
        <el-button type="primary" @click="renameCertain" style="background: #40587a;border-color: #40587a;"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script type="text/ecmascript-6">
  import threePoint from '../../../../static/images/threePoint.png'
  export default {
    props: ["proid"],
    data () {
      return {
        //
        threePoint:threePoint,
        uploadAddress:this.URL.weitianshiLine+"api/v/project/projectUpload",//
        uploadAddressFile:this.URL.weitianshiLine+"api/v/project/uploadFile",//
        project_id: this.proid,
        //
        loading: false,
        //
        groupList: [],
        //
        activeNames: [],
        //()
        bpFileList:[],
        fileList: [],
        //()
        uploadList:[],
        //,
        uploadShow2: {
          lists: [
            {
              bp_type: "1",
              file_title:"2",
              file_id:"3",
              type:"4"
            }
          ]
        },
        //
        dialogFileVisible:false,
        //
        showList: false,
        //
        loadingcheck: false,
        //
        newGroupName:{
          name:''
        },
        //
        typeId:0,
        //
        fileMoveFrame:false,
        //
        radio:0,
        list:[],
        //
        renameFrame:false,
        exGroupName:'',
      }
    },
    methods: {
      //
      initData(file){
        //
        this.$http.post(this.URL.getAllFileType, {
          user_id: localStorage.user_id
        }).then(res => {
//          console.log('fisrt-groupList',res.data.data)
          var groupList = res.data.data;
          //
          this.$http.post(this.URL.getProjectFiles, {
            user_id: localStorage.user_id,
            project_id: this.project_id
          }).then(res => {
            //
            var groupWithFile=res.data.data;
            groupList.forEach(y=>{
              groupWithFile.forEach(x=>{
                if(y.type_id===x.type) {
                  y.file = x.file;
                  y.fileNum = x.count
                }
              })
            })

            //
            if(file){
              console.log('')
              console.log(this.uploadList)
              console.log(this.uploadList[0])
              //
              this.uploadList.forEach((x,index)=>{
                if(x.name===file.name){
                  this.uploadList.splice(index,1)
                }
              })
            }

            //0
            groupList.forEach((x,index)=>{
              if(!x.file){
                x.file=[];
                x.fileNum=0;
              }
              x.newFile=[];
              //,
              if(file){
                this.uploadList.forEach((y,index)=>{
                  if(x.type_id===y.typeId){
                    x.newFile.push(y)
                  }
                })
              }
            })
            this.groupList=groupList;
          })
        })
      },
      //
      toGroup(){
        this.dialogFileVisible = true;
        this.newGroupName.name='';
      },
      //()
      getGroupName(){
        var allGroupName=[]
        this.groupList.forEach(x=>{
          allGroupName.push(x.type_name)
        })
        return allGroupName
      },
      //--
      addGroup() {
        if(!this.$tool.getNull(this.newGroupName.name)){
          if(this.newGroupName.name.replace(/(^\s*)|(\s*$)/g,"").length>20){
            this.$tool.error('20')
          }else if(this.getGroupName().indexOf(this.newGroupName.name)===-1){
            //,
            this.$http.post(this.URL.createFileType,{
              user_id:localStorage.user_id,
              type_name:this.newGroupName.name
            }).then(res=>{
              if(res.data.status_code===2000000){
                this.$tool.success('')
                this.$refs['newGroupName'].resetFields();
                this.dialogFileVisible=false;
                this.initData();
              }else{
                this.$tool.error(res.data.error_msg)
              }
            })
          }else{
            this.$tool.error('')
          }
        }else{
          this.$tool.error('')
        }
      },
      //--
      cancel(){
        this.$refs['newGroupName'].resetFields();
        this.dialogFileVisible = false;
      },
      //typeId()
      getTypeId(typeId,type,groupName){
        this.typeId=typeId;
        if(type===1){
          this.renameGroup(groupName);
        }else if(type===3){
          this.deleteGroup();
        }
      },
      //()
      renameGroup(groupName){
       /* this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          this.$http.post(this.URL.renameFileType,{
            user_id:localStorage.user_id,
            type_id:this.typeId,
            type_name:value
          }).then(res => {
            console.log(res)
            if (res.data.status_code === 2000000) {
              this.loading = false;
              this.$tool.success("")
              this.initData()
            }else{
              this.$tool.error(res.data.error_msg)
            }
          })
        });*/
       this.exGroupName=groupName;
       this.renameFrame=true;
      },
      //
      renameCertain(){
        this.exGroupName=this.$tool.trim(this.exGroupName);
        if(this.exGroupName.length===0){
          this.$tool.error('')
        }else if(this.exGroupName.length>20){
          this.$tool.error('20')
        }else{
          this.$http.post(this.URL.renameFileType,{
            user_id:localStorage.user_id,
            type_id:this.typeId,
            type_name:this.exGroupName
          }).then(res => {
            console.log(res)
            if (res.data.status_code === 2000000) {
              this.loading = false;
              this.$refs['renameFrame'].resetFields();
              this.renameFrame=false;
              this.$tool.success("")
              this.initData();
            } else {
              this.$tool.error(res.data.error_msg)
            }
          })
        }
      },
      //
      renameCancel(){
        this.$refs['renameFrame'].resetFields();
        this.renameFrame=false;
      },
      //
      deleteGroup(){
        this.$confirm(',.?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(this.URL.deleteFileType,{
              user_id:localStorage.user_id,
              type_id:this.typeId,
              project_id:this.project_id
            }).then(res => {
              if (res.data.status_code === 2000000) {
                this.loading = false;
                this.$tool.success("")
                this.initData()
              }else{
                this.$tool.error(res.data.error_msg)
              }
          })
        })
      },
      //()
      beforeUpload(file){
        let filetypes = ['.pdf','.ppt','.pptx', '.doc', '.docx', '.rar', '.zip'];
        //
        let name = file.name;
        let fileend = name.substring(name.lastIndexOf(".")).toLowerCase();
        let isnext = false;
        //
        if (filetypes && filetypes.length > 0) {
          for (var i = 0; i < filetypes.length; i++) {
            if (filetypes[i] == fileend) {
              isnext = true;
              break;
            }
          }
        }
        this.loading = false;
        if (!isnext) {
          this.$tool.error("");
          return false;
        }
        if (parseInt(file.size) > parseInt(20971521)) {
          this.$tool.error("20m");
          return false;
        };

        //typeIduploadList
        file.typeId=this.typeId
        this.uploadList.push(file)

        //newFile
        this.groupList.forEach(x=>{
          if(x.type_id===this.typeId){
            x.newFile.push(file);
          }
        });
      },
      beforeUpload2(file){
        let filetypes = ['.pdf','.ppt','.pptx', '.doc', '.docx', '.rar', '.zip', '.png', '.jpg', '.jpeg'];
        //
        let name = file.name;
        let fileend = name.substring(name.lastIndexOf(".")).toLowerCase();
        let isnext = false;
        //
        if (filetypes && filetypes.length > 0) {
          for (var i = 0; i < filetypes.length; i++) {
            if (filetypes[i] == fileend) {
              isnext = true;
              break;
            }
          }
        }
        this.loading = false;
        if (!isnext) {
          this.$tool.error("");
          return false;
        }
        if (parseInt(file.size) > parseInt(20971521)) {
          this.$tool.error("20m");
          return false;
        };

        //typeIduploadList
        file.typeId=this.typeId
        this.uploadList.push(file)

        //newFile
        this.groupList.forEach(x=>{
          if(x.type_id===this.typeId){
            x.newFile.push(file);
          }
        });
      },
      //,
      handleChange(file, fileList){
        this.loading = true;
        this.activeNames.push('1');
        if (this.loadingcheck) {
          this.loading = false;
          this.loadingcheck = false;
        }
//        console.log('handleChange',file,fileList)
      },
      //
      uploadProgress(event,file,fileList){
        //console,event
        /*console.log('')
        console.log(event)
        console.log(file)
        console.log(fileList)*/
      },
      //
      uploadsuccess(response, file, fileList){
        let data = response.data;
        this.$tool.success("");
        this.loadingcheck = true;
//        console.log('3',response,file)
        this.initData(file)
        //newFile
//        console.log('',this.groupList)
//        console.log(this.uploadList)
      /*  this.uploadList.forEach((x)=>{
          this.groupList.forEach((y,index)=>{
            if(x.typeId===y.type_id){
              y.newFile.push(x);
            }
          })
        })*/
      },
      //
      uploaderror(err, file, fileList){
        this.$tool.error(",")
        this.loadingcheck = false;
        this.loading = false;
      },
      //fileId()
      getFileId(fileId,groupId,bp){
        this.fileId=fileId;
        this.groupId=groupId;
        if(bp){
          this.fileDeal(3)
        }
      },
      //
      fileDeal(command){
        //
        if(parseInt(command)===3){
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$http.post(this.URL.deleteAtFile,{
             user_id:localStorage.user_id,
             file_id:this.fileId
             }).then(res => {
             if (res.data.status_code === 2000000) {
             this.loading = false;
             this.$tool.success("")
             this.initData()
             }else{
             this.$tool.error(res.data.error_msg)
             }
             })
          }).catch(() => {});
        }
        //
        if(parseInt(command)===2){
          this.fileMoveFrame=true;
          this.radio=this.groupId;
        }
      },
      //
      fileMove(){
        console.log(this.radio)
        this.$http.post(this.URL.setFileType,{user_id:localStorage.user_id,file_id:this.fileId,type:this.radio}).then(res=>{
          console.log(res)
          if(res.data.status_code===2000000){
            this.fileMoveFrame=false;
            this.$tool.success('')
            this.initData();
          }else{
            this.$tool.error(res.data.error_msy)
          }
        })
      },
      //
      download(fileId){
          const url=this.URL.weitianshi+this.URL.download+"?user_id="+localStorage.user_id+"&file_id="+fileId;
          window.location.href=url;
      },
      //
      groupchange(label){
        let index = this.groups.index;
        let data = this.groups.group;
        for (let i = 0; i < data.length; i++) {
          if (data[i].value == label) {
            this.groups.name = data[i].label;
            this.uploadShow2.lists[index].type = label;
          }
        }
      },
      //
      saveGroupChange(){//file_id type_id user_id
        let type = this.groups.bp_type;
        let index = this.groups.index;
        let type_name = this.groups.name
        this.$http.post(this.URL.setFileType, {
          user_id: localStorage.user_id,
          file_id: this.uploadShow2.lists[index].file_id,
          type: this.uploadShow2.lists[index].type
        })
        .then(res => {
          if (index !== -1) {
            this.uploadShow2.lists[index].bp_type = type_name;
            this.dialogFileVisible = false
          }
        })
        .catch(err => {
          this.$tool.console(err)
        })

      },
    },
    created(){
      this.initData();
    },
    watch: {}
  }
</script>

<style lang="less">
  @import '../../../assets/css/fileManagement';
  .el-loading-mask{
    display: none;
    /*left:205px!important;*/
    /*top:610px!important;*/
  }
  .el-dialog--small{
    width:390px;
  }
</style>




