<template>
  <div>
    <el-tabs v-model="conmanyName" @tab-click="handleClick">
      <el-tab-pane label="" name="1">

<!--        <div class="block" style="margin-bottom: 22px;">
          <span class="tag-bottom" style="margin-right: 11px;"><img src="../../../assets/images/tag2.png"></span>
          <span class="tag-bottom"></span>
          <span class="tag-bottom"></span>
          <span class="tag-bottom"></span>
          <span class="tag-bottom"></span>
        </div>-->

        <div class="block" style="display: block">
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="20"><div class="content" style="padding-left: 12px;margin-right: -8px;">{{company.company_name}}</div></el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="20"><div class="content" style="padding-left: 12px;margin-right: -8px;">{{company.company_address}}</div></el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{company.company_register_date}}</div></el-col>
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{company.company_team_size}}</div></el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="4"><div class="content left" style="border-bottom:1px solid #e0e6ed;"></div></el-col>
            <el-col :span="8"><div class="content right" style="border-bottom:1px solid #e0e6ed;">{{company.company_email}}</div></el-col>
            <el-col :span="4"><div class="content left" style="border-bottom:1px solid #e0e6ed;"></div></el-col>
            <el-col :span="8"><div class="content right" style="border-bottom:1px solid #e0e6ed;">{{company.company_contact}}</div></el-col>
          </el-row>

        </div>
      </el-tab-pane>
      <el-tab-pane :label="productMessage" name="2">
        <div class="block"  style="display: block">
          <div class="inner fl" v-for="project in company.company_project">
            <div class="img fl"><img src="../../../../assets/images/logo.png"/></div>
            <p class="type fl" style="margin-bottom: 7px;">{{project.company_project_name}}<span>{{project.company_project_type}}</span></p>
            <p class="sec">{{project.company_project_introduce}}</p>
          </div>
        </div>
      </el-tab-pane>
      <!--<el-tab-pane :label="recruitMessage" name="3">
        <div class="bloack">
          <el-table
            :data="recruitData"
            stripe
            style="width: 100%">
            <el-table-column
              prop="position"
              label=""
              width="150">
            </el-table-column>
            <el-table-column
              prop="money"
              label=""
              width="150">
            </el-table-column>
            <el-table-column
              prop="experience"
              label=""
            >
            </el-table-column>
            <el-table-column
              prop="address"
              label=""
            >
            </el-table-column>
            <el-table-column
              prop="date"
              label="">
            </el-table-column>
          </el-table>
          <el-button type="text" style="display: block;margin: 0 auto;color:#5e6d82;margin-top:16px;margin-bottom: 24px;" @click="loadMore"><i class="el-icon-arrow-right"></i></el-button>
        </div>
      </el-tab-pane>-->
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: ["compName"],
    data () {
      return {
        compaName:"", //
        conmanyName: '1',
        productMessage:'',
        recruitMessage:'(29)',
        recruitData: [{
          position: 'JAVA',
          money: '20-30K',
          experience: '1-2',
          address:"",
          date:'2016-05-04'
        }, {
          position: 'IOS',
          money: '1-2K',
          experience: '1',
          address:"",
          date:'2016-05-04'
        }],
        /**/
        company: {
          com_id: 4,
          company_name: "",//
          company_type: "",//()
          company_registered_capital: "",//ff
          company_Operating_state: "",//ss
          company_business_number: "",//123
          company_legal_representative: "",//jdjjf
          company_address: "",//90
          company_empirical_range: "",//
          company_register_date: "",//2014-03-23
          company_organization_code: "",//111
          company_contact: "",//0571-86427664
          company_email: "",//HR@weitianshi.cn
          company_unified_code: "",//111
          company_register_office: "",//111
          team_now_size: "",//500-2000
          company_business_term: "",//111
          company_project:[
/*            {
              project_id: 1,
              company_project_name: "",
              company_project_introduce: "3201232012...",
              company_project_type: "IOS",
              com_id: 4
            }*/
          ]
        },


      }
    },
    methods: {
      handleClick(tab, event) {
//      this.$tool.console(tab, event);
      },
      getCrawlerCompany(){
        this.$http.post(this.URL.getCrawlerCompany, {
          user_id: localStorage.user_id,
          company_name: this.compName
        })
          .then(res => {
            let data=res.data.data;
            data.company.company_register_date=this.$tool.formatDateTime(data.company.company_register_date);
            this.company=data.company;
            this.company.company_project=data.project_product;
            this.productMessage=""+"("+data.project_product.length+")";
          })
          .catch(err => {
            this.$tool.console(err);
          })
      },//

    },
    created(){
//    this.$tool.console(this.compName)
      this.getCrawlerCompany();
    },
    watch: {
      compName: function(e){
//        this.$tool.console(e);
        this.compaName=e;
//        this.$tool.console(this.compaName);
        this.getCrawlerCompany();
      }//
    }

  }
</script>

<style scoped lang="less">

</style>



