<template>
  <div>
      <!--<div class="mark">
        <img src="../../../../assets/images/mark.png">
      </div>-->
    <el-tabs v-model="bussinessName">

      <el-tab-pane label="" name="1" style="margin-top: -30px;">
        <div class="block"  style="display: block">
          <div class="rz-details" style="width: 795px;">
            <div class="rz-detail">
              <p class="det-title"></p>
              <p class="det-info">{{business.company.company_legal_representative}}</p>
            </div>
            <div class="rz-detail">
              <p class="det-title"></p>
              <p class="det-info">{{business.company.company_registered_capital}}</p>
            </div>
            <div class="rz-detail">
              <p class="det-title"></p>
              <p class="det-info">{{business.company.company_operating_state}}</p>
            </div>
          </div>
        </div>
        <div class="block"  style="display: block">
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_type}}</div></el-col>
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_unified_code}}</div></el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_business_number}}</div></el-col>
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_organization_code}}</div></el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_business_term}}</div></el-col>
            <el-col :span="4"><div class="content left"></div></el-col>
            <el-col :span="8"><div class="content right">{{business.company.company_register_office}}</div></el-col>
            <!--            <el-col :span="4"><div class="content left"></div></el-col>
                        <el-col :span="8"><div class="content right"></div></el-col>-->
          </el-row>
          <!--          <el-row :span="24">

                      &lt;!&ndash;<el-col :span="4"><div class="content left"></div></el-col>&ndash;&gt;
                      &lt;!&ndash;<el-col :span="8"><div class="content right"></div></el-col>&ndash;&gt;
                    </el-row>-->
          <el-row :span="24">
            <el-col :span="4"><div class="left hei"></div></el-col>
            <el-col :span="20"><div class="hei2" style="padding: 14px 12px 12px 15px;border:1px solid #e0e6ed;margin-right: -8px;background: #ffffff">{{business.company.company_empirical_range}}</div></el-col>
          </el-row>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="2">
        <div class="bloack change" style="display: block">
          <el-table
            :data="business.company_change"

            border
            style="width: 100%">
            <el-table-column
              prop="company_change_time"
              label=""
              width="150">
            </el-table-column>
            <el-table-column
              prop="company_change_matter"
              label=""
              width="150">
            </el-table-column>
            <el-table-column
              prop="company_change_before"
              label=""
            >
            </el-table-column>
            <el-table-column
              prop="company_change_after"
              label=""
            >
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <!---->
      <el-tab-pane :label="trademarkMessage" name="3">
        <div class="block" style="display: block">
          <el-table
            :data="business.brand"

            border
            style="width: 100%">
            <el-table-column
              prop="company_brand_time"
              label=""
              width="150">
            </el-table-column>
            <el-table-column
              prop="company_brand_name"
              label=""
              width="260">
            </el-table-column>
            <el-table-column
              prop="company_brand_status"
              label=""
            >
            </el-table-column>
            <el-table-column
              prop="company_brand_type"
              label=""
            >
            </el-table-column>
            <el-table-column
              prop="company_brand_registration_number"
              label="">
            </el-table-column>
          </el-table>

        </div>
      </el-tab-pane>
      <!---->
      <!--<el-tab-pane :label="patentMessage" name="4">
        <div class="block patent">
          <el-table
            :data="patentData"
            stripe
            border
            style="width: 100%">
            <el-table-column
              prop="name"
              label=""
              width="280">
            </el-table-column>
            <el-table-column
              prop="publishNubmer"
              label=""
              min-width="110"
              >
            </el-table-column>
            <el-table-column
              prop="publishdate"
              label=""
              min-width="100"
            >
            </el-table-column>
            <el-table-column
              prop="applyNumber"
              label=""
              min-width="120"
            >
            </el-table-column>
            <el-table-column
              prop="classify"
              label=""
              min-width="145"
              >
            </el-table-column>
          </el-table>

        </div>
      </el-tab-pane>-->
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: ["comid"],
    data () {
      return {
        bussinessName: '1',
        trademarkMessage:'',
        patentMessage:'(12)',
        patentData:[{
          name:"",
          publishNubmer:"CN105741643A",
          publishdate:"2016-04-20",
          applyNumber:"201610248895X",
          classify:"G09B19/16(2006.01)I"

        }],
        business:{
          //
          company: {
            com_id: 4,
            company_name: "",//
            company_type: "",//
            company_registered_capital: "1000",//
            company_Operating_state: "",//
            company_business_number: "110108015068911",//
            company_legal_representative: "",//
            company_address: "90",//
            company_empirical_range: " 1.  ",//
            company_register_date: "2014-03-23",//
            company_organization_code: "1112121212",//
            company_contact: "0571-86427664",//
            company_email: "HR@weitianshi.cn",//
            company_unified_code: "111",//
            company_register_office: "",//
            team_now_size: "500-2000",//
            company_business_term: "2012-07-10  2032-07-09",//
          },
          //
          brand: [{
            com_id: 4,
            company_brand_type: "20",
            company_brand_registration_number: "17680971A",
            company_brand_status: "",
            company_brand_name: "",
            company_brand_time: "2016-05-04"
          }],
          //
          company_change: [{
            com_id: 4,
            company_change_time: "",//2015-08-17
            company_change_matter: "",//
            company_change_before: "",//
            company_change_after: ""//
          }]
        }
      }
    },
    methods: {
      getCrawlerBrand(){
        this.$http.post(this.URL.getCrawlerBrand, {
          user_id: localStorage.user_id,
          com_id: this.comid
        })
          .then(res => {
            let data=res.data.data;
            this.business=data;
            console.log(this.business);
            this.trademarkMessage=""+"("+data.brand.length+")"
          })
          .catch(err => {
            this.$tool.console(err);
          })
      }//
    },
    created(){
      this.getCrawlerBrand();
    },
    watch : {
      comid : function(e){
        this.com_id=e;
        this.getCrawlerBrand();
        this.$tool.console(e);
      }//id
    }
  }
</script>

<style lang="less">
  .mark{
    position: absolute;
    left: 191px;
    top: -14px;
    width: 8px;
    height: 8px;
    z-index: 1000;
    img{
      width: 100%;
    }
  }
  .change{
    .cell{
      margin:16px 0 19px;
      vertical-align: super;
    }
    .is-leaf{
      .cell{
        margin:0px;
      }
    }
  }
  .patent{
    .cell{
      font-size:12px;
      color:#1f2d3d;
    }
  }


</style>



